@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.card {
  position: relative;
  perspective: 800px;
  -moz-perspective: 800px;

  height: var(--card-size);
  width: var(--card-size);

  border-radius: 5px;
  transition: all 0.2s ease;
}

.card:hover {
  cursor: pointer;
  transform: translateY(-1px);
}

.card__side {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);

  height: var(--card-size);
  width: var(--card-size);

  transition: all 0.8s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card__side--front {
  background-image: linear-gradient(
    to bottom right,
    rgb(250, 109, 84),
    rgb(148, 26, 26)
  );
  /* transform: rotateY(0deg); */
  font-size: 2em;
  font-weight: bold;
}

.card__side--front:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.card__side--back {
  background-color: rgb(255, 255, 255);
  transform: rotateY(180deg);
  z-index: -1;
}

.card.rotated .card__side--front {
  transform: rotateY(180deg);
}

.card.rotated .card__side--back {
  transform: rotateY(360deg);
  overflow: hidden;
}

.card img {
  height: 80%;
  width: auto;
  border-radius: 5px;
  object-fit: cover;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --card-size: 140px;
}

html {
  box-sizing: border-box;
}

body {
  font-family: Nunito, sans-serif;
}

.app {
  width: 600px;
  margin: 40px auto;

  display: grid;
  grid-template-columns: repeat(4, 160px);
  grid-template-rows: repeat(3, 160px);
  grid-auto-rows: 160px;
  justify-items: center;
  align-items: center;
  grid-gap: 20;
  gap: 20;
}

.scoreboard {
  grid-column: 1 / 5;
  padding: 0 35px;

  color: white;

  width: 100%;
  height: 140px;
  background-image: linear-gradient(
    to bottom,
    rgb(250, 109, 84),
    rgb(148, 26, 26)
  );
  border: 10px solid rgb(148, 26, 26);
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4),
    inset 0 3px 10px rgba(255, 255, 255, 0.8);

  display: flex;
  justify-content: space-between;
  align-items: center;

  overflow: hidden;
}

span {
  display: block;
  text-align: center;
  font-size: 3em;
  font-weight: 900;
  margin-top: -10px;
}

.scoreboard h3 {
  margin-bottom: -10px;
}

.message {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.btn {
  width: 50%;

  margin: 10px 0;
  padding: 10px;

  font-weight: 900;
  color: rgb(148, 26, 26);

  border-radius: 5px;
  outline: none;
  border: none;
  transition: all 0.2s;
}

.btn:hover {
  cursor: pointer;
  background-color: rgb(148, 26, 26);
  color: white;
  transform: scale(1.02);
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.8);
}

.btn:active {
  box-shadow: 0 1px 5px rgba(255, 255, 255, 0.8);
  transform: scale(1);
}

